import React from 'react';
import {S} from '../Reviews_Styles';


export const ReviewsTabs: React.FC<{ author: Array<object> }> = (props: { author: Array<object> }) => {
    return (
        <S.ReviewTabs>
            {
                props.author.map((a: any) => {
                    return (
                        <S.ReviewTab>
                            <S.Avatar width={"48px"} height={"48px"} src={a.authorAvatar} alt=""/>
                            <S.Author>{a.authorName}</S.Author>
                        </S.ReviewTab>
                    )
                })
            }
        </S.ReviewTabs>
    );
};

